E,CAAA,eACA,CAAA,2DACF,SACE,CAAA,iBACA,CAAA,OACA,CAAA,QACA,CAAA,CAAA,qCAER,qDAIM,eACE,CAAA,yDACA,UACE,CAAA,iBACA,CAAA,4EACF,cACE,CAAA","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Alata&family=Jost:wght@200;300;400;500;600;700;800;900&family=Julius+Sans+One&family=Lexend+Giga:wght@100;200;300;400;500;600;700;800;900&family=Sen:wght@400;700;800&display=swap')\n","@import './styles/_fonts'\n\n$color_background: #DFE0DB\n$color_hamburger_bg: #4F5052\n$color_pink: #F76BC5\n$color_blue: #3DB5C7\n$color_black: #333333\n$color_white: #FFFDF7\n\n$height_nav: 72px\n\n// 置中\n@mixin center\n  position: absolute\n  left: 50%\n  top: 50%\n  transform: translate(-50%, -50%)\n\n// 三大粉紅色標題文字(about\\works\\contact)\n@mixin large_top_title\n  color: $color_pink\n  font-family: 'Julius Sans One', sans-serif\n  font-size: 52px\n  letter-spacing: 12px\n  margin: 0 0 40px 0\n\n@mixin small_top_title\n  font-size: 40px\n  letter-spacing: 8px\n  margin: 0\n\n// 粉紅色標題線\n@mixin line\n  width: 30px\n  height: 3px\n  background-color: $color_pink\n  border: 1px\n  border-radius: 3px\n  margin: 3px 0\n\n// 粉紅色標題線旁標題\n@mixin large_title\n  margin: 0 0 0 16px\n  font-size: 28px\n  letter-spacing: 6px\n\n// 年份區塊\n@mixin year\n  color: #ffffff\n  font-size: 16px\n  background-color: $color_pink\n  opacity: 1\n  padding: 2px 6px\n  margin: 0\n\nhtml\n  font-size: 10px\n\nbody\n  margin: 0\n  padding: 0\n  width: 100vw\n  height: 100vh\n  background: $color_background\n\n*\n  color: $color_black\n  letter-spacing: 2px\n  vertical-align: middle\n  position: relative\n  box-sizing: border-box\n  -webkit-user-select: none\n  user-select: none\n  font-family: 'Sen', '儷黑 Pro', 'LiHei Pro', '微軟正黑體', 'Microsoft JhengHei', sans-serif\n\n#root\n  height: 100%\n\n//- nav\nnav.nav\n  position: fixed\n  width: 100%\n  height: $height_nav\n  z-index: 12\n  display: flex\n  align-items: center\n  background: $color_background\n\n  .nav_content\n    display: flex\n    position: relative\n    justify-content: space-between\n    align-items: center\n    height: 40px\n    width: 100%\n    margin: 0px 32px\n    .nav_left\n      img\n        z-index: 2\n        width: 60%\n        cursor: pointer\n        transition: 0.3s\n        &:hover\n          transform: rotate(5deg)\n    input#nav_switch\n      display: none\n    .nav_hamburger-lines\n      display: none\n    .menu-items\n      display: flex\n      div.item\n        list-style: none\n        a.nav_text\n          text-decoration: none\n          color: $color_pink\n          font-family: 'Julius Sans One', sans-serif\n          font-size: 28px\n          letter-spacing: 8px\n          cursor: pointer\n          transition: 0.3s\n          display: inline-block\n          &:hover\n            color: $color_blue\n            transform: translate(4px, 4px)\n\n      div.item ~ div.item\n        margin: 0 0 0 40px\n\n    input#nav_switch:checked+.menu-items\n      color: red\n      font-size: 100px\n\n\n\n//- header\nheader.header\n  height: 100%\n  padding: $height_nav 0\n  margin-bottom: 120px\n\n  .header_content\n    width: 60%\n    height: 100%\n    margin: auto\n    //- 上方圖logo 圖片區\n    .header_top\n      height: calc(100% - 300px)\n      width: 100%\n      margin-top: 36px\n      img.header_top_logo\n        height: 100%\n        width: 100%\n    //- 下方文字區\n    .header_bottom\n      width: 100%\n      text-align: center\n      position: absolute\n      bottom: 36px\n      left: 0\n      right: 0\n      display: flex\n      flex-direction: column\n      align-items: center\n      .header_bottom_content\n        display: flex\n        flex-direction: column\n        align-items: center\n        margin: 0 0 16px 0\n        .header_bottom_title\n          color: $color_blue\n          font-size: 2rem\n          margin: 0 0 8px 0\n        .header_bottom_text\n          font-size: 1.8rem\n          margin: 0 0 8px 0\n          line-height: 26px\n        //- 按鈕\n      .header_bottom_button\n        width: 48px\n        height: 48px\n        img\n          width: 48px\n          cursor: pointer\n          transition: 0.3s\n          &:hover\n            transform: translateY(0.3rem)\n\n// about 和 works 共通設定\n.container\n  width: 70%\n  margin: 80px auto\n  .container_title\n    +large_top_title\n\n//- about\n.about.container\n  //- 教育程度、技能、證書\n  .about_group\n    display: flex\n    justify-content: space-between\n    .about_top_content\n      width: 30%\n      .about_top_title\n        display: flex\n        align-items: center\n        .about_top_title_line\n          hr\n            +line\n        .about_top_title_text\n          +large_title\n      .about_top_image\n        img\n          width: 100%\n          margin: 10px 0 28px 0\n      .about_top_detail\n        display: flex\n        align-items: center\n        .about_top_detail_content\n          width: 100%\n          .about_top_detail_content_block\n            display: flex\n            justify-content: space-between\n\n    //- 教育程度\n    .about_top_content\n      .about_top_detail\n        display: flex\n        flex-direction: column\n        .about_top_detail_wrapper ~ .about_top_detail_wrapper\n          margin: 24px 0 0 0\n        .about_top_detail_wrapper\n          width: 100%\n          display: flex\n          align-items: center\n          .about_top_detail_content\n            width: 100%\n            .about_top_detail_content_block\n              display: flex\n              flex-direction: column\n              .about_top_detail_content_block_top\n                display: flex\n                justify-content: space-between\n                p.chinese\n                  margin: 0\n                  font-size: 20px\n                .year_wrapper\n                  p.year\n                    +year\n              .about_top_detail_content_block_botton\n                p.english\n                  font-size: 14px\n                  margin: 12px 0 0 0\n    //- 技能\n    .about_top_content.skills