er_block_detail_year_wrapper\n                p.year\n                  +year\n                p.year.width\n                  padding: 2px 30px\n            .about_other_block_detail_text\n              font-size: 20px\n              color: rgba($color_black, 0.7)\n              margin: 24px 0 0 0\n\n//- works\n.works.container\n  //- works 上方標題文字及圖片\n  .works_title_block\n    .works_title_wrapper\n      display: flex\n      align-items: center\n      .works_title_line\n        hr\n          +line\n      .works_title_text\n        +large_title\n    .works_title_image\n      img\n        width: 100%\n        margin: 10px 0 0 0\n  //- 毛天使、欣湛、瑪莉雅之吻、雅莉珊卓、富盛豐共同設定\n  .works_block\n    display: flex\n    flex-direction: column\n    margin: 60px 0\n    .works_block_title_wrapper\n      display: flex\n      align-items: center\n      .works_block_title_icon\n        margin: 0 16px 0 0\n        img\n          width: 36px\n      .works_block_title_name\n        font-size: 24px\n      pre.works_block_title_sort\n        font-size: 18px\n        margin: 0 0 0 14px\n    // 遮罩\n    .fadeout\n      position: absolute\n      bottom: 100px\n      left: 0\n      width: 100%\n      height: 200px\n      background-image: linear-gradient(to bottom, transparent, #dfe0db)\n    // MORE 按鈕\n    button.works_more_button\n      cursor: pointer\n      margin: 32px auto\n      padding: 8px 24px\n      font-size: 2.4rem\n      text-align: center\n      font-weight: 500\n      letter-spacing: 4px\n      border: 2px solid $color_blue\n      background-color: $color_blue\n      color: $color_background\n      border-radius: 60px\n      box-shadow:1px 1px 4px 1px rgba($color_blue, 0.25)\n      transition-duration: 0.3s\n      &:hover\n        transform: scale(1.1)\n      &.is_open\n        border: 2px solid $color_black\n        background-color: $color_black\n        box-shadow:1px 1px 4px 1px rgba($color_black, 0.25)\n    // 各個隱藏高度尺寸\n    .label_wrapper\n      height: 320px\n      overflow: hidden